@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-size: 1.2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 10em;
  color: black;
  margin: 0;
  font-weight: 500;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.8rem;
  color: rgb(135, 150, 22);
}

h4 {
  font-size: 2rem;
}

.bg-img {
  max-width: 100%;
  height: 100vh;
}

.item-unvisible {
  visibility: hidden;
}
@media (min-width: 1024px) {
  .item-unvisible {
    visibility: visible;
  }
}

.item-visible {
  visibility: visible;
}

:export {
  headerMaxHeight: 6;
}

.header {
  position: fixed;
  background-color: rgb(195, 214, 47);
  z-index: 1001;
}

.header-logos {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  width: 100vw;
  padding: 0.5em 0 0.5em;
}

.header-logos img {
  max-height: var(--header-height, 6em);
  min-height: var(--header-height, 6em);
}

hr {
  margin: 0;
  height: 2px;
}

.carousel-mb .overlay {
  position: fixed;
  top: 0;
  background-color: rgba(0, 0, 0, 0.2);
  width: 100vw;
  height: 100vh;
  z-index: -1;
}
.carousel-mb img {
  width: 100vw;
  height: 100vh;
}
@media (min-width: 1024px) {
  .carousel-mb {
    display: none;
  }
}

.carousel-dp {
  display: none;
}
@media (min-width: 1024px) {
  .carousel-dp {
    display: block;
  }
  .carousel-dp .overlay {
    position: absolute;
    top: 0;
    background-color: rgba(0, 0, 0, 0.3);
    width: 100vw;
    height: 100vh;
    z-index: -1;
  }
  .carousel-dp img {
    width: 100vw;
    height: 100vh;
  }
}

.carousel {
  position: fixed;
  top: 0;
  width: 100%;
  margin: auto;
  overflow: hidden;
  z-index: -2;
}
.carousel ul {
  display: flex;
  padding: 0;
  margin: 0;
  width: 400%;
  animation: change 20s infinite ease;
}
.carousel li {
  width: 100;
  list-style: none;
}

@keyframes change {
  0% {
    margin-left: 0;
  }
  15% {
    margin-left: 0;
  }
  40% {
    margin-left: -100%;
  }
  55% {
    margin-left: -100%;
  }
  80% {
    margin-left: -200%;
  }
  100% {
    margin-left: -200%;
  }
}
.custom-header {
  position: relative;
  width: 100vw;
  height: 30vh;
  z-index: 104;
  display: flex;
  justify-content: center;
}
@media (min-width: 1024px) {
  .custom-header {
    display: block;
    position: fixed;
    top: 0;
    width: 100vw;
    height: 15vh;
    z-index: 104;
    display: flex;
    justify-content: center;
    overflow: hidden;
  }
}

.custom-header-elements {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em;
}
.custom-header-elements .logo {
  width: auto;
  height: 9.5em;
}
.custom-header-elements .menu {
  cursor: pointer;
  margin-top: 1em;
  width: auto;
  height: 3.5em;
}
@media (min-width: 1024px) {
  .custom-header-elements .menu {
    display: none;
  }
}
@media (min-width: 1024px) {
  .custom-header-elements {
    flex-direction: row;
    padding: 0.5em;
    background-color: rgba(255, 255, 255, 0.8);
  }
  .custom-header-elements .logo {
    width: auto;
    height: 6em;
  }
}

.items-header {
  display: none;
}
@media (min-width: 1024px) {
  .items-header {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: end;
    cursor: pointer;
  }
  .items-header h3 {
    padding: 0 2em;
    font-size: 2.4rem;
  }
}

.custom-footer {
  background-color: rgba(255, 255, 255, 0.7);
  padding: 1em 0;
  width: 100%;
  z-index: 102;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}
@media (min-width: 1024px) {
  .custom-footer {
    flex-direction: row;
    justify-content: space-between;
    height: 15vh;
  }
  .custom-footer hr {
    height: 4.5em;
    width: 0.2em;
    margin: 0 0.5em;
    background-color: black;
  }
}

.items-footer {
  padding: 0.3em;
  cursor: pointer;
}
.items-footer h3 {
  font-size: medium;
  margin: 0.1em 0;
}
@media (min-width: 1024px) {
  .items-footer {
    width: 25%;
    display: flex;
    justify-content: space-around;
  }
  .items-footer h3 {
    font-weight: 300;
    font-size: 1.6em;
    margin: 0.1em 0em;
  }
}

.custom-footer-logos {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  padding: 0.3em;
}
.custom-footer-logos img {
  width: auto;
  height: 3em;
}
@media (min-width: 1024px) {
  .custom-footer-logos {
    width: 50%;
  }
  .custom-footer-logos img {
    width: auto;
    height: 5em;
  }
}

.policies {
  padding-top: 0.5em;
}
@media (min-width: 1024px) {
  .policies {
    text-align: center;
    width: 25%;
    padding: 0 2em;
  }
}

.overlay-menu {
  visibility: hidden;
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
  justify-content: center;
  overflow: hidden;
}
.overlay-menu .logo {
  padding: 1em 5em;
  width: auto;
  height: 11.5em;
  overflow: hidden;
}
@media (min-width: 1024px) {
  .overlay-menu {
    display: none;
  }
}

.items-overlay {
  position: absolute;
  top: 15em;
}
.items-overlay h3 {
  margin: 0.3em 0;
  cursor: pointer;
}

.bt-close {
  cursor: pointer;
  position: absolute;
  top: 25em;
}

.section {
  position: relative;
  z-index: 103;
  display: flex;
}
@media (min-width: 1024px) {
  .section {
    align-items: center;
    height: 70vh;
    margin-top: 15vh;
  }
}

.section-elements {
  display: flex;
  flex-direction: column;
  margin: 3em 0;
}
@media (min-width: 1024px) {
  .section-elements {
    flex-direction: row;
  }
}

.section-element {
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 1em;
  margin: 1em 1em;
  padding: 1.5em;
}

.section-images {
  text-align: center;
}
.section-images img {
  width: 15em;
}
@media (min-width: 1024px) {
  .section-images img {
    width: 20em;
  }
}

.section-info {
  text-align: center;
}
.section-info p {
  font-size: 1em;
  color: black;
}

.all-routes-container {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  /* flex: 1 1 0px; */
}

.all-routes-route-container {
  margin-top: 1em;
  display: flex;
  flex-direction: row;
  cursor: pointer;
}

.all-routes-route-id {
  margin: 0 0.5em 0 1em;
  min-width: 60px;
  max-width: 60px;
  min-height: 60px;
  max-height: 60px;
  text-align: center;
  color: #fff;
  font-size: 30px;
  line-height: 55px;
  font-weight: bold;
}

.all-routes-route-name {
  font-weight: bolder;
}

.all-routes-route-description {
  margin-right: 1em;
}

.all-routes-title {
  font-weight: bolder;
  text-align: center;
  margin: 1em;
  padding: 0.5em;
  background-image: url(../../public/img/background_lines.jpg);
}

/* Bootstrap 5 grid screen sizes
  Extra small (<576px) 	Small (>=576px) 	Medium (>=768px) 	Large (>=992px) 	Extra Large (>=1200px) 	XXL (>=1400px) */
@media screen and (min-width: 576px) {
  .all-routes-container > div {
    width: 75vw;
  }
}
@media screen and (min-width: 1200px) {
  .all-routes-container > div {
    margin-top: 15vh;
    margin-bottom: 5em;
    width: 50vw;
  }
}