.all-routes-container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    /* flex: 1 1 0px; */
}

.all-routes-route-container {
    margin-top: 1em;
    display: flex;
    flex-direction: row;
    cursor: pointer;
}

.all-routes-route-id {
    margin: 0 .5em 0 1em;
    min-width: 60px;
    max-width: 60px;
    min-height: 60px;
    max-height: 60px;
    text-align: center;
    color: #fff;
    font-size: 30px;
    line-height: 55px;
    font-weight: bold;
}

.all-routes-route-name {
    font-weight: bolder;
}

.all-routes-route-description {
    margin-right: 1em;
}

.all-routes-title {
    font-weight: bolder;
    text-align: center;
    margin: 1em;
    padding: .5em;
    background-image: url(../../public/img/background_lines.jpg);
}

/* Bootstrap 5 grid screen sizes
  Extra small (<576px) 	Small (>=576px) 	Medium (>=768px) 	Large (>=992px) 	Extra Large (>=1200px) 	XXL (>=1400px) */

@media screen and (min-width: 576px) {
    .all-routes-container>div {
        width: 75vw;
    }
}

@media screen and (min-width: 1200px) {
    .all-routes-container>div {
        margin-top: 15vh;
        margin-bottom: 5em;
        width: 50vw;
    }
}